// Action
const changeCurrentSongAction = (currentSong) => ({
  type: actionType.CHANGE_CURRENT_SONG,
  currentSong,
})

// Action
export const changeSongIndexAction = (index) => {
  // Index
  setCurrentSongIndex(index)
  return {
    type: actionType.CHANGE_CURRENT_SONG_INDEX,
    index,
  }
}

